import React, { Component } from 'react';

class Home extends Component {
  constructor(props) {
    super(props);

    this.state = { 
        sort:'descending',
        seletedNationality:'English',
        nationalities:['English','Belgian','American','Dutch','Turkish']
    };
    
  }
  
  componentDidMount(){
    this.props.getList({sort : this.state.sort,nationality:this.state.seletedNationality});
  }

  renderStudentsList(){
    return (
      this.props.Students.map((val,k)=>(<div key={'Students'+k} className="tr">
      <div className="td">{val.id}</div>
      <div className="td">{val.firstName}</div>
      <div className="td">{val.lastName}</div>
      <div className="td">{val.age}</div>
      <div className="td">{val.nationality}</div>
    </div>))
    )
  }

  onChange(e){
    this.setState({seletedNationality:e.target.value})
    this.props.getList({nationality : e.target.value,sort:this.state.sort})
  }

  SortList(e){
    this.setState({sort:this.state.sort==='ascending'?'descending':'ascending'},()=>{
      this.props.getList({sort : this.state.sort,nationality:this.state.seletedNationality})
    })
  }

  render() {
    return (
      <div className="App col-lg-10 mx-auto mb-5">
        <p className=" mt-5 " style={{  marginBottom:0,  'fontSize': '20px'}}>
          <span className="float-left mr-3" >Students list</span>
          <select className="form-control" style={{'width': '200px','float': 'left'}} onChange={this.onChange.bind(this)}>
            {(this.state.nationalities.map((x,i)=><option key={'nationalities'+i} value={x}>{x}</option>))}
          </select>

          <button onClick={this.props.ShowAddPage} className="btn btn-primary float-right mb-2 mr-2">Add</button>
        </p>
        <div className="table card"  style={{minHeight:'200px'}}>
          <div className="thead">
            <div className="tr" >
              <div className="th">ID</div>
              <div className="th">First Name</div>
              <div className="th">Last Name</div>
              <div className="th">Age</div>
              <div className="th">Nationality</div>
            </div>
          </div>
          <div className="tbody">
            {this.renderStudentsList()}
          </div>
         {!this.props.Students.length?<p className="No-Items-Found mt-5">No Items Found
            <button className="btn btn-link ml-3" onClick={this.props.InitStudents}> Init Students </button>
          </p>:null}
          
      </div>
        <button className={"btn col-3  "+(this.state.sort==='descending'?'btn-primary':'btn-success')}  onClick={ this.SortList.bind(this) }> Sort</button>
      </div>
    );
  }

}

export default Home;