import React, { Component } from 'react';
import axios from 'axios';
import swal from 'sweetalert2';


class AddStudent extends Component {
constructor(props) {
    super(props);
    this.state = { 
        id:'',
        firstName:'',
        lastName:'',
        age:'',
        nationality:'English',
        nationalitiesArr:['English','Belgian','American','Dutch','Turkish']
    };
  }

 
  handleChange(e) {
    const { name, value } = e.target;
    this.setState({ [name]: value });
  }

  handleSubmit(e) {
    e.preventDefault();
    if(!this.state.id||!this.state.firstName||!this.state.lastName
      ||!this.state.nationality||!this.state.age){return swal.fire({
          icon: 'error',
          title: 'Missing Info',
          text: 'please Add All stduent info',
        });
      }
      
      axios.post(`http://localhost:3200/new`,this.state).then((d)=>{
        if(d.status===200){
          swal.fire({  
            title: ' A new student added successfully ',
            icon: 'success'
           });
           this.Close();
        }else{
          swal.fire({  
            title: '  Adding new student failed ',
            text:d.data.Error,
            icon: 'error'
           });
        }
      }).catch((e)=>{
        swal.fire({  
          title: '  Adding new student failed ',
          text:e,
          icon: 'error'
         });
      });
      
  }

  Close(){
    this.setState({id:'',
    firstName:'',
    lastName:'',
    age:'',
    nationality:'English'});
    this.props.CloseAddPage({reload:true});
  }

  render() {
    return (
        <div className="col-12 addPage" style={{paddingTop:'100px'}}>
          <form name="form" className="col-lg-6  p-4 card m-auto" style={{marginTop:'100px !important'}} onSubmit={this.handleSubmit.bind(this)}>
            <h5 className="mb-3"> New Student </h5>

            <div className={'form-group'}>
                <label htmlFor="id">ID</label>
                <input type="text" className="form-control" name="id" value={this.state.id} onChange={this.handleChange.bind(this)} />
            </div>

              <div className={'form-group'}>
                  <label htmlFor="firstName">First Name</label>
                  <input type="text" className="form-control" name="firstName" value={this.state.firstName} onChange={this.handleChange.bind(this)} />
              </div>

              <div className={'form-group'}>
                  <label htmlFor="lastName">Last Name</label>
                  <input type="text" className="form-control" name="lastName" value={this.state.lastName} onChange={this.handleChange.bind(this)} />
              </div>

              <div className={'form-group'}>
                  <label htmlFor="age">Age</label>
                  <input type="text" className="form-control" name="age" value={this.state.age} onChange={this.handleChange.bind(this)} />
              </div>

              <div className={'form-group'}>
                  <label htmlFor="nationality" className="float-left mr-4">Nationality</label>
                  <select className="form-control"  name="nationality" style={{'width': '200px','float': 'left'}}  onChange={this.handleChange.bind(this)}>
                    {(this.state.nationalitiesArr.map((x,i)=><option key={'nationalities'+i} value={x}>{x}</option>))}
                  </select>
              </div>
              

              <div className="form-group mt-3">
                <button className="btn btn-default border" onClick={this.Close.bind(this)} >Cancel</button>
                <button className="btn btn-primary float-right">Save</button>
              </div>

          </form>
      </div>
    );
  }
}

export default AddStudent;