import React, { Component } from 'react';
import axios from 'axios';
import swal from 'sweetalert2';

import Home from './Pages/Home';
import AddStudent from './Pages/AddStudent';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    this.state = { 
        showAddPage:false,
        Students:[],
        by:{}
    };
    
  }


  ShowAddPage(){
    console.log('======');
    this.setState({showAddPage:true})
  }

  CloseAddPage(val){
    console.log('======');
    this.setState({showAddPage:false});
    if(val.reload){this.getList(this.state.by)}
  }


  InitStudents(){
    swal.fire({
      title: 'Do you wnat to Init Students DB ?',
      text: 'this will add the default Students list',
      html: '',
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Yes',
      cancelButtonText: 'No' }).then(()=>{
        axios.post(`http://localhost:3200/init`,this.state).then((d)=>{
          if(d.status===200){
            swal.fire({  
               title: '  Students DB Initiated successfully ',
               icon: 'success'
              });
              this.getList();
          }
        }).catch((e)=>{
          swal.fire({  
            title: ' Init Students DB  failed ',
            text:e,
            icon: 'error'
           });
        });
      });
  }

  getList(by){
    let url = `http://localhost:3200/students`;
    if(by){for(let k in by){if(by[k] && by[k] !== 'undefined' && by[k] !== 'All'){url=url+(new RegExp("\\?").test(url)?'&':'?')+`${k}=${by[k]}`}}}
    this.setState({by})
    axios.get(url).then((d)=>{
      this.setState({Students:d.data});
    }).catch((e)=>{
      swal.fire({  
        title: ' get Students List failed',
        text:e,
        icon: 'error'
       });
    });

  }

render() {
  return (    
    <div className="App">
      <header className="header border-bottom">
        <div className="col-10 p-2 m-auto">
          <ul className="list-group" style={{'flexDirection': 'row'}}>
            <li className="list-group-item header-item brand">DESelect</li>
            <li className="list-group-item header-item">Home</li>
            <li className="list-group-item header-item">Page 1</li>
            <li className="list-group-item header-item">Page 2</li>
            <li className="list-group-item header-item">Page 3</li>
          </ul>
        </div>
        
      </header>

        {this.state.showAddPage?<AddStudent 
        CloseAddPage={this.CloseAddPage.bind(this)} />:null}

        <Home 
          getList={this.getList.bind(this)}
          InitStudents={this.InitStudents.bind(this)}
          ShowAddPage={this.ShowAddPage.bind(this)} 
          Students={this.state.Students}  />

      <footer className="footer border-top">
        <div className="col-10 p-2 m-auto">
          copyright Â© 2021 all rights reserved
        </div>
      </footer>
      </div>
    );
  }
}

export default App;
